export default {
	name: 'PulishLost',
	data() {
		return {
			content: '',
			imgList: [],
			opened: false,
			sex: true,
			concatName: '',
			concatPhone: '',
			images: [],
			originalImageList: []
		}
	},
	methods: {
		getData () {
			this.$http({
				url: this.$urlPath.getLostInfo,
				params: {
					lid: this.$routeParams.lid
				},
				onRequestSuccess: (res) => {
					const info = res.data
					if (info) {
						if (info.images) {
							this.imgList = info.images.split(',')
							this.originalImageList = this.imgList
						}
						this.content = info.description
						this.concatName = info.concat_name.replace(/先生/g, '').replace(/女士/, '')
						this.concatPhone = info.concat_phone
						this.opened = Number(info.is_local) === 0
					}
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		chooseImage() {
			if (this.imgList.length >= 3) {
				this.$toast('最多上传三张图片')
				return
			}
			uni.chooseImage({
				count: 3 - this.imgList.length, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		viewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		delImg(e) {
			uni.showModal({
				title: '提示',
				content: '确定要删除此图片吗？',
				cancelText: '不删除',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		changeSex(e) {
			this.sex = e.detail.value
		},
		open (e) {
			this.opened = e.detail.value
		},
		upload () {
			this.$http({
				url: this.$urlPath.editLost,
				params: {
					lid: this.$routeParams.lid,
					content: this.content,
					name: this.concatName + (this.sex ? '先生' : '女士'),
					phone: this.concatPhone,
					images: this.images.length > 0 ? this.originalImageList.concat(this.images).join(',') : this.originalImageList.join(','),
					isLocal: this.opened ? '0' : '1'
				},
				onRequestSuccess: (res) => {
					this.$toast('发布成功')
					uni.$emit('refreshLost')
					this.$back()
				},
				onRequestFail: (errorCode, error) => {
					this.$toast(error)
				}
			})
		},
		publish () {
			if (!this.content) {
				this.$toast('请输入描述信息')
				return
			}
			if (this.content.length < 10) {
				this.$toast('请至少输入10字描述信息')
				return
			}
			if (!this.concatName) {
				this.$toast('请输入联系人姓名')
				return
			}
			if (!this.concatPhone) {
				this.$toast('请输入联系人电话')
				return
			}
			if (!this.$utils.validator.isPhone(this.concatPhone)) {
				this.$toast('请输入合法的手机号')
				return
			}
			const tempList = this.imgList.filter(it => {
				return it.indexOf('http') !== -1
			})
			if (tempList.length > 0) {
				this.$upload({
					files: tempList,
					uploadSuccess: (imagePath) => {
						this.images.push(imagePath)
						if (this.images.length === tempList.length) {
							this.upload()
						}
					}
				})
			} else {
				this.upload()
			}
		}
	},
	onLoad() {
		this.getData()
	}
}
